<template>
	<div id='login_logo'>
		<div class='login_con'>新增车辆页面</div>
		<div class='login_form' >
				<el-form v-model='carMsgForm' :rules='rules'>
					<!-- 第一行 -->
					<el-row>
							<el-col :span='6' class='fontSize' >
								<el-form-item label='车场名称' prop='parkingId' class='laeblStyle'>
									<el-select v-model='carMsgForm.parkingId' class='handle-input' v-show='parkingIdOptionsShow'>
										<el-option
										v-for='item in parkingIdOptions'
										:key='item.id'
										:label='item.label'
										:value='item.value'>
										</el-option>
									</el-select>
								</el-form-item>
							</el-col>
							<el-col :span='6'>
								<el-form-item label='车牌号' prop='plateNo' class='laeblStyle'>
									<el-input v-model='carMsgForm.plateNo'  class="handle-input"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span='6'>
								<el-form-item label='车辆分类' prop='plateNo' class='laeblStyle'>
									<el-select v-model='carMsgForm.vehicleTypeId' class="handle-input" v-show='vehicleTypeOptionsShow'>
										<el-option
										v-for='item in vehicleTypeOptions'
										:key='item.key'
										:value="item.value"
										:label='item.label'>
										</el-option>
									</el-select>
								</el-form-item>
							</el-col>
					</el-row>
					<!-- 第二行 -->
					<el-row>
						<el-col :span='6'>
								<el-form-item label='车辆颜色' prop='vehicleColor' class='laeblStyle' >
										<el-input v-model='carMsgForm.vehicleColor'  class="handle-input"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span='6'>
									<el-form-item label='电子车牌号' prop='rfidNo' class='laeblStyle'>
										<el-input v-model='carMsgForm.rfidNo'  class="handle-input"></el-input>
									</el-form-item>
							</el-col>
							<el-col :span='6'>
							<el-form-item label='身份证' prop='ownerIdNo' class='laeblStyle'>
									<el-input v-model='carMsgForm.ownerIdNo'  class="handle-input"></el-input>
							</el-form-item>
						</el-col>
					</el-row>
					<!-- 第三行 -->
					<el-row>
						<el-col :span='6'>
							<el-form-item label='车辆所有人' prop="vehicleOwner" class='laeblStyle'>
									<el-input v-model='carMsgForm.vehicleOwner'  class="handle-input"></el-input>
							</el-form-item>
						</el-col>
						<el-col :span='6'>
							<el-form-item label='性别' prop="ownerGender" class='laeblStyle' style='text-align: center'>
								<el-radio v-model='carMsgForm.ownerGender' label='1' >男</el-radio>                      
                      			<el-radio v-model='carMsgForm.ownerGender' label='2' >女</el-radio> 
							</el-form-item>
						</el-col>
						<el-col :span='6'>
							<el-form-item label='生日' prop="ownerBirth" class='laeblStyle'>
											<el-date-picker
												v-model="carMsgForm.ownerBirth"
												type="date"
												placeholder="选择日期" class='handle-input'>
											</el-date-picker>
							</el-form-item>
						</el-col>
					</el-row>

						<!-- 第四行 -->
						<el-row>
							<el-col :span='6'>
								<el-form-item label='手机号码' prop='ownerPhone' class='laeblStyle'>
										<el-input v-model='carMsgForm.ownerPhone'  class="handle-input"></el-input>
								</el-form-item>
							</el-col>
							<el-col :span='6' >
								<el-form-item label='车牌颜色' prop='plateColor' class='laeblStyle'>
									<el-select v-model='carMsgForm.plateColor' class='handle-input' v-if='plateColorOptionsShow'>
										<el-option
										v-for='item in plateColorOptions'
										:key='item.id'
										:label='item.label'
										:value='item.value'>
										</el-option>
									</el-select>
								</el-form-item>
							</el-col>

							<el-col :span='6'>
								<el-form-item label='车牌品牌型号' prop='vehicleModel' class='laeblStyle'>
									<el-input v-model='carMsgForm.vehicleModel' style='width:180px;display: inline-block'></el-input>
								</el-form-item>
							</el-col>
						</el-row>
						<el-row>
							<el-col :span='12'>
								<el-form-item label='地址' prop='ownerAddress' >
										<el-input v-model='carMsgForm.ownerAddress' style='width:80%' ></el-input>
								</el-form-item>
							</el-col>
						</el-row>
						<!-- 按钮行 -->
						<div style='float:right;margin:20px 35% 0 0;'>
								<el-button class="el-button el-button--warning" style='margin-right:30px;width:80px' @click='cancel()'>取消</el-button>
							<el-button class="el-button el-button--primary" style='width:80px' @click='addCar(carMsgForm.vehicleType)'>新增</el-button>
						</div>
						<div style="clear:both"></div>
				</el-form>
		</div>
	</div>
</template>
<script>
	import moment from 'moment'
export default{
	data(){
		var validatorRule=function(rule,value,callback){
			if(!value){
				callback(new Error(rule.message));
			}
		}
		return{
			rules:{
				// parkingId:[{required:true,message:"车场信息不能为空",tigger:blur,validator: validatorRule}]
			},
			// 车辆信息
			carMsgForm:{},
			plateTypeOptions:[
				// {id:1,value:1,label:"蓝"},
				// {id:2,value:2,label:"绿"},
				// {id:3,value:3,label:"白"},
				// {id:4,value:4,label:"黑"},
				// {id:5,value:5,label:"黄"},
			],
			vehicleTypeOptions:[],
			vehicleTypeOptionsShow:false,
			plateTypeOptions:[
			],
			isReletOptions:[
				{id:0,value:0,label:"不自动"},
				{id:1,value:1,label:"自动"},
			],
			typeNo:0,
			plateColorOptions:[],
			plateColorOptionsShow:false,
			//车场名称 &id
			parkingIdOptions:[],
			parkingIdOptionsShow:false,
			detailType:false,


		}
	},
	mounted() {
		this.getCarColor();
		this.getParkingId();
		this.getMonthType();
		this.getCarType();
	},
	methods: {
		cancel(){

		},
		addCar(val){
			var self=this;
			console.log(self.carMsgForm.vehicleTypeId);
			// 判断车辆类型
				var params=self.carMsgForm;
				var params={
					"parkingId":self.carMsgForm.parkingId,
					"vehicleClassifyId":self.carMsgForm.vehicleTypeId,
					"plateNo":self.carMsgForm.plateNo,
					"plateColorNo":self.carMsgForm.plateColor,

					"plateColor":self.plateColorOptions[this.carMsgForm.plateColor-1].label,

					"vehicleColor":self.carMsgForm.vehicleColor,
					"vehicleModel":self.carMsgForm.vehicleModel,
					"vehicleOwner":self.carMsgForm.vehicleOwner,
					"rfidNo":self.carMsgForm.rfidNo,
					// "vehicleType":self.vehicleTypeOptions[this.carMsgForm.vehicleType-1].label,
					"ownerIdNo":self.carMsgForm.ownerIdNo,
					'ownerGender':self.carMsgForm.ownerGender,
					"ownerBirth":self.momentTime(self.carMsgForm.ownerBirth),
					"ownerPhone":self.carMsgForm.ownerPhone,
					"ownerAddress":self.carMsgForm.ownerAddress
				}
				self.axiosCar(params);
		},
		momentTime(val){
			return moment(val).format("YYYY-MM-DD");
		},
		momentTime1(val){
			return moment(val).format("YYYY-MM-DD HH:mm:ss");
		},
		changeType(val){
			// console.log("val")
			// console.log(val)
			// if(val=='1'||val=='3'){
			// 	this.typeNo=1;
			// }else if(val=='2'){
			// 	this.typeNo=2;
				
			// }else{
			// 	this.typeNo=3;
			// }
		},
		//添加车辆请求
		axiosCar(params){
			var self=this;
			var url=self.baseUrl+"vehicleInfo/add";
			self.$axios.post(url,params).then(function(res){
				if(res.data.status=='0'){
					self.$message({
						message:res.data.message,
						type:"success"
					})
				}else{
					self.$message({
						message:res.data.message,
						type:"info"
					})
				}
			}).catch(function(err){
				console.log(err);
			})
		},
		// 车牌颜色
		getCarColor(){
			var self=this;
			var url=self.baseUrl+"vehicleInfo/plateColorList"
			self.$axios.get(url).then(function(res){
				self.plateColorOptionsShow=true;
				if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.plateColorOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].plateColor,
							value:res.data.data[i].plateColorNo,
						}
					}
				}
			}).catch(function(err){
				console.log(err);
			})
		},
		//车辆名称
		getParkingId(){
			var self=this;
			var url=self.baseUrl+"vehicleInfo/initParkingList?userId="+localStorage.getItem("userId");
			self.$axios.get(url).then(function(res){
				self.parkingIdOptionsShow=true;
				if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.parkingIdOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].parkingName,
							value:res.data.data[i].id
						}
					}
				}
			}).catch(function(err){
				console.log(err);
			})
		},
		// 车辆类型
		getCarType(){
			var self=this;
			var url=self.baseUrl+"vehicleInfo/initVehicleClassify"
			self.$axios.get(url).then(function(res){
				self.vehicleTypeOptionsShow=true;
				if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.vehicleTypeOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].vehicleClassifyName,
							value:res.data.data[i].id,
						}
					}
				}
			}).catch(function(err){
				console.log(err);
			})
		},
		// 月租类型
		getMonthType(){
			var self=this;
			var url=self.baseUrl+"vehicleInfo/vehicleMonthlyTypeList"
			self.$axios.get(url).then(function(res){
				console.log(res.data.data.length)
				console.log(res.data.data)
				for(var i=0;i<res.data.data.length;i++){
					self.plateTypeOptions[i]={
						id:res.data.data[i].id,
						label:res.data.data[i].monthlyType,
						value:res.data.data[i].id 
					}
				}
			}).catch(function(err){
				console.log(err);
			})
		}
	},
}
</script>
<style scoped>
	#login_box{
		width:100%;
		height:100%;
		background:#fff;
		overflow: hidden;
	}
	.login_con{
		font-size:20px;
		font-weight: bold;
		text-align: left;
	}
	.login_form{
		width:100%;
		height:70%;
		padding:30px 20px;
	}
	.handle-input{
		width:196px;
		display: inline-block;
		/* margin-left:-40px; */
	}
.fontSize{
	font-size:20px !important;
}
.laeblStyle{
	width:280px;
	text-align: right;
}
.handleInput2{
	width:200px;
	display: inline-block;
}
.yuezuStyle{
	width:315px;
	text-align: right;
}
</style>


